html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Asap", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #464646;
  font-size: 1.5rem;
}

.contenedor {
  width: 95%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Asap", sans-serif;
  margin: 2.5rem 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.5rem;
}

/** Media Queries **/
h1.h1 {
  font-weight: 600;
  margin: 50px 0;
}
@media (max-width: 480px) {
  h1.h1 {
    font-size: 2.8rem;
  }
}
h1.h1 .list {
  color: #3DA9E5;
}

.contenedor {
  -webkit-box-shadow: 0px 5px 30px 5px rgb(189, 189, 189);
  -moz-box-shadow: 0px 5px 30px 5px rgb(189, 189, 189);
  box-shadow: 0px 5px 30px 5px rgb(189, 189, 189);
  border-radius: 1.5rem;
  padding: 5rem 4rem 3rem 4rem;
  display: grid;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  min-height: 40rem;
}

.side1 {
  position: relative;
  display: flex;
  justify-content: center;
}
.side1 .formulario {
  display: flex;
  flex-direction: column;
}
.side1 .caja-tarea {
  width: 40rem;
  height: 10rem;
  border: 2px solid #BDBDBD;
  border-radius: 0.5rem;
  padding: 1rem;
  word-wrap: break-word;
  resize: none;
}
.side1 .caja-tarea:focus-visible {
  outline: none;
}
.side1 .enter {
  position: absolute;
  top: -4rem;
  right: 0;
  left: 50%;
  bottom: 0;
  transform: translateX(-35%);
  color: #BDBDBD;
  z-index: -1;
}
.side1 .boton-agregar {
  color: #797979;
  width: 15rem;
  height: 2.5rem;
  align-self: center;
  margin: 3.5rem;
  background-color: #fff;
  font-weight: 400;
  transition: 0.1s ease;
  border: 2px solid #BDBDBD;
  border-radius: 0.5rem;
}
.side1 .boton-agregar:hover {
  cursor: pointer;
}

.side2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.side2 .buscador {
  color: #BDBDBD;
  height: 2.5rem;
  margin: -3.5rem 0 1.5rem 0;
  display: none;
  width: 60%;
}
.side2 .buscador.click {
  outline: 2px solid #BDBDBD;
  border-radius: 0.5rem;
}
.side2 .buscador .icon-share {
  font-size: 2rem;
  display: flex;
  margin-top: 0.4rem;
  align-items: center;
}
.side2 .buscador .icon-share:hover {
  cursor: pointer;
}
.side2 .buscador .texto-buscar {
  border: none;
  height: 100%;
  width: 100%;
  border-radius: 0.5rem;
}
.side2 .buscador .texto-buscar:focus-visible {
  outline: none;
}
.side2 .toggle {
  width: 80%;
  height: 2.5rem;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  margin-top: -0.5rem;
}
.side2 .toggle:hover {
  cursor: pointer;
}
.side2 .toggle .tarea {
  text-align: center;
  background-color: #F8F8F8;
}
.side2 .toggle .tarea.toggle-active {
  background-color: #dcdcdc;
}
.side2 .toggle .tareas {
  border-radius: 0.5rem 0 0 0.5rem;
}
.side2 .toggle .completadas {
  border-radius: 0 0.5rem 0.5rem 0;
}
.side2 .contain {
  display: block;
  margin-top: 2rem;
  height: 24rem;
  overflow-y: scroll;
  padding-right: 0.5rem;
}
.side2 .contain .container-task {
  width: 40rem;
  height: 10rem;
  flex-direction: column;
  margin-bottom: 2rem;
  position: relative;
}
.side2 .contain .container-task .card-task {
  width: 100%;
  height: 100%;
  border: 2px solid #BDBDBD;
  border-radius: 0.5rem;
  padding: 1rem 2.5rem 0 2.5rem;
  word-wrap: break-word;
  resize: none;
  background-color: #fff;
}
.side2 .contain .container-task .card-task:focus-visible {
  outline: none;
}
.side2 .contain .container-task .card-task:hover {
  cursor: text;
}
.side2 .contain .container-task .tag {
  position: absolute;
  bottom: 0.5rem;
  left: 0.5rem;
  cursor: pointer;
  border: none;
  width: 8rem;
  height: 2rem;
  border-radius: 1rem;
  font-size: 1rem;
  font-weight: 600;
}
.side2 .contain .container-task .tag.green {
  background-color: #82f694;
}
.side2 .contain .container-task .tag.oranje {
  background-color: #feb078;
}
.side2 .contain .container-task .tag.red {
  background-color: #e07a7a;
}
.side2 .contain .container-task .icon {
  position: absolute;
}
.side2 .contain .container-task .icon:hover {
  color: #cfcfcf;
  cursor: pointer;
}
.side2 .contain .container-task .icon-close {
  top: -5px;
  right: 2px;
  font-size: 2.5rem;
  color: #dcdcdc;
}
.side2 .contain .container-task .icon-edit {
  bottom: -9px;
  right: 3px;
  font-size: 2rem;
  color: #dcdcdc;
}
.side2 .contain .container-task .boton-check {
  width: 2rem;
  height: 2rem;
  border: 0.32rem solid #dcdcdc;
  background-color: #fff;
  border-radius: 50%;
  top: 0.3rem;
  left: 0.3rem;
}
.side2 .contain .container-task .boton-check:hover {
  cursor: pointer;
}
.side2 .contain .container-task .boton-check .icon-check {
  position: absolute;
  font-size: 2rem;
  color: #fff;
  top: -6px;
  right: 0;
  visibility: hidden;
}
.side2 .contain .container-task .checked {
  background-color: #72f25e;
  border-color: #cfcfcf;
  border: none;
}
.side2 .contain .container-task .checked > .icon-check {
  visibility: visible;
  pointer-events: none;
}
.side2 .contain .any-task {
  font-size: 1.6rem;
  color: #BDBDBD;
  margin-top: 8rem;
  display: block;
}
.side2 .contain .any-task.hide {
  display: none;
}
.side2 .contain .any-completed {
  font-size: 1.6rem;
  color: #BDBDBD;
  margin-top: 8rem;
  display: block;
}
.side2 .contain .any-completed.hide {
  display: none;
}
.side2 .contain.hidde {
  display: none;
}
.side2 .delete-all {
  color: #797979;
  width: 15rem;
  height: 2.5rem;
  align-self: center;
  margin: 3.5rem;
  background-color: #fff;
  font-weight: 400;
  transition: 0.1s ease;
  border: 2px solid #BDBDBD;
  border-radius: 0.5rem;
  background-color: transparent;
  color: #e07a7a;
  border-color: #e07a7a;
}
.side2 .delete-all:hover {
  cursor: pointer;
}